.btn-gradient-blue {
    background: rgb(2,54,139);
    background: linear-gradient(298deg, rgba(2,54,139,1) 0%, rgba(2,54,139,1) 100%);
    /*background: rgb(54,123,255);*/
    /*background: linear-gradient(90deg, rgba(54,123,255,1) 0%, rgba(0,80,147,1) 100%);*/
    box-shadow: 2px 2px 4px rgba(0,0,0,0.1);
    color: white;
    font-size: 1rem;
    transform: translateY(0);
    transition: 1s ease;
    -webkit-transition: 1s ease;
    -moz-transition: 1s ease;
}
